import { gql } from "apollo-boost";

export const GET_USERS = gql`
    query getUsers {
        getUsers {
            id
            email
            first_name
            last_name
        }
    }
`;

export const GET_USER_BY_ID = gql`
    query getUserById($id: Int!) {
        getUserById(id: $id) {
            email
            first_name
            last_name
        }
    }
`;

export const GET_EVENT_BY_ID = gql`
    query getEventById($id: Int!) {
        getEventById(id: $id) {
            id
            title
            host {
                id
                first_name
                last_name
            }
            description
            date_of_event
            start_time_of_event
            end_time_of_event
            location
            invitations {
                id
                guest {
                    id
                    first_name
                    last_name
                }
                event {
                    id
                }
                attendance
                comment
            }
        }
    }
`;

//Local cashe
export const LOGGED_IN_USER = gql`
    query loggedInUser {
        loggedInUser @client {
            id
        }
    }
`;

export const GET_CACHED_EVENTS = gql`
    query getCachedEvents {
        events @client {
            id
            title
            host {
                id
                first_name
                last_name
            }
            description
            date_of_event
            start_time_of_event
            end_time_of_event
            location
            invitations {
                id
                guest {
                    id
                    first_name
                    last_name
                }
                event {
                    id
                }
                attendance
                comment
            }
        }
    }
`;
