import "./style/Input.css";

import React, { useState } from "react";

export const Submit = (props) => {
    return (
        <input
            type="submit"
            key={props.key ? props.key : props.id}
            name={props.name ? props.name : props.id}
            id={props.id}
            value={props.value}
            className={`${props.cssButton} button`}
        />
    );
};

export const Button = (props) => {
    return (
        <input
            type="button"
            key={props.key ? props.key : props.id}
            name={props.name ? props.name : props.id}
            id={props.id}
            value={props.value}
            onClick={props.onClick}
            className={`${props.cssButton} button`}
        />
    );
};

export const Radio = (props) => {
    return (
        <>
            <label htmlFor={props.id}>{props.labelText}</label>
            <input
                type="radio"
                key={props.key ? props.key : props.id}
                id={props.id}
                name={props.name ? props.name : props.id}
                value={props.value}
                onChange={props.onChange}
                checked={props.compare === props.value}
            />
        </>
    );
};

export const Field = (props) => {
    const [active, setActive] = useState("");
    const handleActive = (event) => {
        if (event.type === "mouseover" || event.type === "focus") {
            setActive("active");
        } else if (
            event.type === "mouseleave" &&
            document.activeElement.id !== event.target.name
        ) {
            setActive("");
        } else if (event.type === "blur") {
            setActive("");
        }
    };
    return (
        <div className="container">
            <label
                className={`${props.cssLabel} label ${
                    props.value ? "active" : active
                }`}
                htmlFor={props.id}
            >
                {props.labelText}
            </label>
            <input
                className={`${props.cssField} field ${active} ${
                    !!props.validate ? "validate" : ""
                }`}
                key={props.key ? props.key : props.id}
                id={props.id}
                name={props.name ? props.name : props.id}
                type={props.type}
                value={props.value}
                onChange={props.onChange}
                onMouseOver={handleActive}
                onMouseLeave={handleActive}
                onFocus={handleActive}
                onBlur={handleActive}
            />
            <p className="message">{props.validate}</p>
        </div>
    );
};
