import React, { useState } from "react";
import { Mutation } from "@apollo/react-components";
import { Field, Button, Submit } from "./Inputs";
import { CREATE_USER, CREATE_EVENT } from "../gql/mutators";

const CreateUser = () => {
    const [values, setValues] = useState({
            firstName: "",
            lastName: "",
            eMail: "",
            password: "",
            passwordRepeat: "",
            validate: {
                firstName: "",
                lastName: "",
                eMail: "",
                password: "",
                passwordRepeat: "",
            },
        }),
        {
            firstName,
            lastName,
            eMail,
            password,
            passwordRepeat,
            validate,
        } = values;

    const handleChange = (event) => {
            event.persist();
            if (event.target.name.match(/^(password|passwordRepeat)$/)) {
                setValues((values) => ({
                    ...values,
                    [event.target.name]: event.target.value,
                    validate: {
                        ...values.validate,
                        password: "",
                        passwordRepeat: "",
                    },
                }));
            } else {
                setValues((values) => ({
                    ...values,
                    [event.target.name]: event.target.value,
                    validate: { ...values.validate, [event.target.name]: "" },
                }));
            }
        },
        handleSubmit = (createUser, event) => {
            event.preventDefault();
            if (password !== passwordRepeat) {
                setValues((values) => ({
                    ...values,
                    validate: {
                        ...values.validate,
                        password: "Password Mismatch",
                        passwordRepeat: "Password Mismatch",
                    },
                }));
            } else {
                createUser({
                    variables: {
                        user: {
                            first_name: firstName,
                            last_name: lastName,
                            email: eMail,
                        },
                        password: password,
                    },
                });
            }
        };
    return (
        <div>
            <Mutation mutation={CREATE_EVENT} onCompleted="">
                {(createUser, { loading, error }) => (
                    <form
                        onSubmit={(event) => {
                            handleSubmit(createUser, event);
                        }}
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            alignItems: "center",
                            width: "500px",
                        }}
                    >
                        <Field
                            type="text"
                            value={firstName}
                            id="firstName"
                            onChange={handleChange}
                            labelText="First Name"
                            validate={validate.firstName}
                        />
                        <Field
                            type="text"
                            value={lastName}
                            id="lastName"
                            onChange={handleChange}
                            labelText="Last Name"
                            validate={validate.lastName}
                        />
                        <Field
                            type="email"
                            value={eMail}
                            id="eMail"
                            onChange={handleChange}
                            labelText="E-Mail"
                            validate={validate.eMail}
                        />
                        <Field
                            type="password"
                            value={password}
                            id="password"
                            onChange={handleChange}
                            labelText="Password"
                            validate={validate.password}
                        />
                        <Field
                            type="password"
                            value={passwordRepeat}
                            id="passwordRepeat"
                            onChange={handleChange}
                            labelText="Repeat Password"
                            validate={validate.passwordRepeat}
                        />
                        <Submit value="Submit" cssButton="submit" />
                    </form>
                )}
            </Mutation>
        </div>
    );
};

export default CreateUser;
